import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { permissionsLabels } from 'src/app/shared/models/fmosNewRolePermissions';
import { RoleService } from 'src/app/shared/services/role.service';
import { RuleService } from 'src/app/shared/services/rule.service';
import { AddSystemService } from 'src/app/shared/services/add-system.service';
import Swal from 'sweetalert2';
import { AppConstants } from 'src/app/shared/config/app.constant';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'npr-rule-translation-summary',
  templateUrl: './rule-translation-summary.component.html',
  styleUrls: ['./rule-translation-summary.component.scss']
})
export class RuleTranslationSummaryComponent implements OnInit {
  translationList: any = [];
  translationDeatils: any;
  dtTrigger: any = new Subject();
  dtOptions: DataTables.Settings = {};
  isLoading:boolean = false;
  roleCodes = new permissionsLabels();
  ruleTRespData: any;
  currentUser: any;
  authBtnHide: boolean;
  editBtn: boolean;
  authBtn:boolean;
  deleted: boolean;
  reopenBtn: boolean;
  showdelete: boolean;
  closeBtn: boolean;
  authorizeDone: boolean;
  statusArr: String[] = ['Authorized', 'Unauthorized', 'Open', 'Close'];
  totalPages: any;
  totalRecords: any;
  API_URL = AppConstants.baseURL;
  constructor(private ruleService: RuleService, private router:Router,private roleService:RoleService,
    private cdr:ChangeDetectorRef,private addSystem: AddSystemService , private http: HttpClient) { }

  ngOnInit(): void {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength : 5,
      columnDefs: [ { type: 'date', 'targets': [3] } ],
      order: [[3, 'desc']],
      serverSide: true,
      // processing: true,
      lengthMenu: [
        [5, 10, 20, 30,-1],
        [5, 10, 20, 30,"ALL"],
      ],
      ajax: (dataTablesParameters: any, callback) => {
        
        dataTablesParameters.minNumber = dataTablesParameters.start + 1;
        dataTablesParameters.maxNumber =
          dataTablesParameters.start + dataTablesParameters.length;
        console.log(
          dataTablesParameters.minNumber,
          dataTablesParameters.maxNumber
        );
        const pageNo = Math.floor(dataTablesParameters.minNumber / dataTablesParameters.length);
        const pageSize = dataTablesParameters.length;
        const search=dataTablesParameters.search.value
        this.http.get<any>(`${this.API_URL}`+`/msgApi/getTranslation?pageNo=${pageNo}&pageSize=${pageSize}&search=${search}`)
          .subscribe(resp => {
            ;
            console.log(resp);
            for (let data of resp.result) {
              data.approvedStatus = this.statusArr.find((i) =>
                i.startsWith(data.approvedStatus)
              );
      
              data.recordStatus = this.statusArr.find((i) =>
                i.startsWith(data.recordStatus)
              );
            }
            
            this.totalPages = resp.totalNoOfPages;
            this.totalRecords = resp.totalRecords;
            this.translationList = resp.result;
            this.isLoading = false;
            this.cdr.markForCheck();

            callback({
              recordsTotal: resp.totalRecords,
              recordsFiltered: resp.totalRecords,
              data: [],
            });
          });
      },
      // columnDefs: [ { type: 'date', 'targets': [5] } ],
      // order: [[5, 'desc']],

    };
    this.currentUser = localStorage.getItem('userFromLogin');
    // this.fetchRuleTranslationSummary();
    this.roleService.fetchScreenPermissions('Rule translation');
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);
  }

  fetchRuleTranslationSummary() {
    this.isLoading = true;
    this.ruleService.fetchingruleTranslation()
      .subscribe(resp => {
        console.log(resp);
        for (let data of resp.result) {
          data.approvedStatus = this.statusArr.find((i) =>
            i.startsWith(data.approvedStatus)
          );
  
          data.recordStatus = this.statusArr.find((i) =>
            i.startsWith(data.recordStatus)
          );
        }
        this.translationList = resp.result;
        this.isLoading = false;
        this.cdr.markForCheck();

        this.dtTrigger.next();
      })
  }

  onGetId(row) {
    console.log(row);
    this.router.navigate(['/rule-translation/edit', row.transId]);
  }

  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  }
 close(data){
  this.ruleTRespData = data;
  
  Swal.fire(
    {
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: 'You are trying to close record. ' + ' Do you want to proceed?',
      showCancelButton: true,
      confirmButtonColor: '#3e54b8',
      cancelButtonColor: '#d33',
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: 'NO',
      confirmButtonText: 'YES', 'icon': 'info', iconColor: "#d33"
    }
  ).then((result => {
    console.log("this is reopen ", result);
    if (result.isConfirmed === true) {
      let RuleTranslationData: any = this.ruleTRespData;
      this.addSystem.editruleTranslation("close", this.currentUser, RuleTranslationData).subscribe((response) => {
        console.log(response);
        if (response) {
          Swal.fire({
            text: "Record is Closed",
            icon: 'success'
          }).then(() => window.location.reload());
          this.ruleTRespData = response;
          this.editBtn = true;
          this.authBtn = true;
          this.deleted = false;
          this.reopenBtn = false;
        }
      });
    }
  
  }))
  
   

 }
 open(data){
   this.ruleTRespData = data;
  
   
  Swal.fire(
    {
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: 'You are trying to re-open record. ' + ' Do you want to proceed?',
      showCancelButton: true,
      confirmButtonColor: '#3e54b8',
      cancelButtonColor: '#d33',
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: 'NO',
      confirmButtonText: 'YES', 'icon': 'info', iconColor: "#d33"
    }
    
  ).then((result => {
    console.log("this is reopen ", result);
    
    if (result.isConfirmed === true) {
      let RuleTranslationData: any = this.ruleTRespData;
      this.addSystem.editruleTranslation("open", this.currentUser, RuleTranslationData).subscribe((response) => {
        console.log(response);
        if (response) {
          Swal.fire('Record is Reopened').then(() => window.location.reload());
          this.ruleTRespData = response;
          this.authBtnHide = false;
          this.editBtn = false;
          this.authBtn = false;
          
        }
        
      });
    }
  }))
 
 }
 delete(data){
  this.ruleTRespData = data;
  
   
  Swal.fire(
    {
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: 'You are trying to Delete record. ' + ' Do you want to proceed?',
      showCancelButton: true,
      confirmButtonColor: '#3e54b8',
      cancelButtonColor: '#d33',
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: 'NO',
      confirmButtonText: 'YES', 'icon': 'info', iconColor: "#d33"
    }
  ).then((result => {
    console.log("this is reopen ", result);
    if (result.isConfirmed === true) {
      let RuleTranslationData: any = this.ruleTRespData;
      console.log(this.ruleTRespData);
      this.addSystem.deleteRuleTranslation(this.currentUser, RuleTranslationData.transId).subscribe((response) => {
        // console.log(response.Text);
        // if(response){

        // }
      }, (err) => {
        if (err.status == 200) {
          this.showdelete = true;
          this.editBtn = true;
          this.authBtn = true;
          this.closeBtn = true;

          Swal.fire({
            title: 'Record is Deleted!',
            confirmButtonText: 'Ok'
          }).then(() => window.location.reload());

        }
        else {
          Swal.fire("Failed to delete the reocrd");
        }
      });
    }
  }))
  

  }

  auth(data){
    this.ruleTRespData = data;
  console.log( this.currentUser); 
  console.log(this.ruleTRespData.creatorId)

    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Authorize record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
           confirmButtonText: 'YES','icon':'info'
      }
    ).then((result => {
      console.log("this is reopen ",result);
      if(result.isConfirmed=== true){
        if (this.currentUser == this.ruleTRespData.creatorId) {
          Swal.fire('Maker cannot authorize!');
       }
       else {
         let RuleTranslationData: any = this.ruleTRespData;
         this.addSystem.editruleTranslation("auth", this.currentUser, RuleTranslationData).subscribe((response) => {
           console.log(response);
           if (response.approverId) {
             this.authorizeDone = true;
             Swal.fire('Record is Authorized ').then(() => window.location.reload());
             this.ruleTRespData = response;
           }
         });
       }
     }}))

  }


  
}
