  showFormat: any;

  matlabel1: string;
  matlabel2: string;
  headerTagData: any;
  messageBasisIso: any;
  messageKeyIso: any;
  xmlRespData: any;
  sourcesysData: any;
  translationResp: any;
  transId: any;
  fileName: any;
  disableSubmit: boolean = false;
  disableReset: boolean = false;
  uploadXmlResp: any;
  desSystem: any[] = [];
  mtype: string[];
  isoRespData: any = [];
  tempArrayList: any[] = [];
  fieldNoList: { messageKey: any, messagebasis: any, dataType: any, defaultValue: any }[] = [];
  dbConnectionList: any[] = [];
  @ViewChild('fileWrapper') fileUploadWrapper: ElementRef;
  roleCodes = new permissionsLabels();
